import { useState } from "react";
import { NavLink } from "react-router-dom";

// @mui material components
import Card from "@mui/material/Card";
import Switch from "@mui/material/Switch";

// Material Dashboard 2 React components
import MDBox from "components/MDBox";
import MDTypography from "components/MDTypography";
import MDInput from "components/MDInput";
import MDButton from "components/MDButton";
// import { TextField } from "@mui/material";
// Authentication layout components
import BasicLayout from "layouts/authentication/components/BasicLayout";

// Images
import bgImage from "assets/images/bg3.jpg";
import logo from "../../../assets/images/FT-small-white.png";
// import classes from "../../../App.module.css";
import classes from "./signin.module.css";

function Basic() {
  const [rememberMe, setRememberMe] = useState(false);

  const handleSetRememberMe = () => setRememberMe(!rememberMe);

  return (
    <BasicLayout image={bgImage}>
      <Card className={classes.Authform}>
        <MDBox pt={4} pb={3} px={3} borderRadius="lg">
          <img className={classes.Logo} src={logo} alt="Logo" />
          <MDBox component="form" role="form">
            <MDBox mb={2}>
              <MDInput id="label" type="email" label="Email" fullWidth />
            </MDBox>
            <MDBox mb={2}>
              <MDInput className="label" type="password" label="Password" fullWidth />
            </MDBox>
            {/* <TextField type="email" label="Email" variant="outlined" color="primary" fullWidth /> */}
            <MDBox display="flex" alignItems="center" ml={-1}>
              <Switch checked={rememberMe} onChange={handleSetRememberMe} />
              <MDTypography
                variant="button"
                fontWeight="regular"
                color="light"
                onClick={handleSetRememberMe}
                sx={{ cursor: "pointer", userSelect: "none", ml: -1 }}
              >
                &nbsp;&nbsp;Remember me
              </MDTypography>
            </MDBox>
            <MDBox mt={4} mb={1} component={NavLink} to="/">
              <MDButton className={classes.btnfc} variant="gradient" color="error" fullWidth>
                sign in
              </MDButton>
            </MDBox>
          </MDBox>
        </MDBox>
      </Card>
    </BasicLayout>
  );
}

export default Basic;
