const dummyData = [
  {
    value: " ",
    label: " ",
  },
  {
    value: "option 1",
    label: "option 1",
  },
  {
    value: "option 2",
    label: "option 2",
  },
  {
    value: "option 3",
    label: "option 3",
  },
  {
    value: "option 4",
    label: "option 4",
  },
  {
    value: "option 5",
    label: "option 5",
  },
  {
    value: "option 6",
    label: "option 6",
  },
];

const chargeCharter = [
  {
    value: " ",
    label: " ",
  },
  {
    value: "Yes",
    label: "Yes",
  },
  {
    value: "No",
    label: "No",
  },
];

const aircraft = [
  {
    value: "HeliA",
    label: "Heli-A",
  },
  {
    value: "HeliB",
    label: "Heli-B",
  },
  {
    value: "HeliC",
    label: "Heli-C",
  },
  {
    value: "HeliD",
    label: "Heli-D",
  },
  {
    value: "ATLNS",
    label: "ATL NOSHOW",
  },
];

export default function Bookingform() {
  return (
    <Box pt={2}>
      <MDBox pt={0} pb={1} px={1}>
        <p>Booking Form</p>
      </MDBox>
      <div>
        <TextField
          select
          label="Aircraft Tail Number"
          defaultValue="HeliA"
          SelectProps={{
            native: true,
          }}
          sx={{ m: 1 }}
          size="small"
        >
          {aircraft.map((option) => (
            <option key={option.value} value={option.value}>
              {option.label}
            </option>
          ))}
        </TextField>
        <TextField type="date" size="small" sx={{ m: 1 }} />
        <TextField
          select
          label="PAX Type"
          defaultValue="HeliA"
          SelectProps={{
            native: true,
          }}
          sx={{ m: 1 }}
          size="small"
        >
          {aircraft.map((option) => (
            <option key={option.value} value={option.value}>
              {option.label}
            </option>
          ))}
        </TextField>
        <TextField
          select
          label="Time"
          defaultValue="HeliA"
          SelectProps={{
            native: true,
          }}
          sx={{ m: 1, width: "11ch" }}
          size="small"
        >
          {aircraft.map((option) => (
            <option key={option.value} value={option.value}>
              {option.label}
            </option>
          ))}
        </TextField>
        <TextField
          select
          label="Package"
          defaultValue="HeliA"
          SelectProps={{
            native: true,
          }}
          sx={{ m: 1, width: "11ch" }}
          size="small"
        >
          {dummyData.map((option) => (
            <option key={option.value} value={option.value}>
              {option.label}
            </option>
          ))}
        </TextField>
        <TextField
          select
          label="Agent Name"
          defaultValue="HeliA"
          SelectProps={{
            native: true,
          }}
          sx={{ m: 1, width: "11ch" }}
          size="small"
        >
          {aircraft.map((option) => (
            <option key={option.value} value={option.value}>
              {option.label}
            </option>
          ))}
        </TextField>
        <TextField label="Agent Booking Ref." sx={{ m: 1, width: "11ch" }} size="small" />
        <TextField
          select
          label="Number of PAX"
          defaultValue=" "
          SelectProps={{
            native: true,
          }}
          sx={{ m: 1, width: "11ch" }}
          size="small"
        >
          {aircraft.map((option) => (
            <option key={option.value} value={option.value}>
              {option.label}
            </option>
          ))}
        </TextField>
        <TextField label="Lead PAX" sx={{ m: 1, width: "11ch" }} size="small" />
        <TextField label="PAX E-Mail" sx={{ m: 1, width: "11ch" }} size="small" />
        <TextField label="PAX Phone" sx={{ m: 1, width: "11ch" }} size="small" />
        <TextField label="PAX Weight" sx={{ m: 1, width: "11ch" }} size="small" />
        <TextField
          select
          label="Payment Status"
          defaultValue=" "
          SelectProps={{
            native: true,
          }}
          sx={{ m: 1, width: "11ch" }}
          size="small"
        >
          {aircraft.map((option) => (
            <option key={option.value} value={option.value}>
              {option.label}
            </option>
          ))}
        </TextField>
        <TextField label="Discount" sx={{ m: 1, width: "11ch" }} size="small" />
        <TextField
          select
          label="Comp. Upgrade From"
          defaultValue=" "
          SelectProps={{
            native: true,
          }}
          sx={{ m: 1, width: "11ch" }}
          size="small"
        >
          {aircraft.map((option) => (
            <option key={option.value} value={option.value}>
              {option.label}
            </option>
          ))}
        </TextField>
        <TextField label="Cost Overide" sx={{ m: 1, width: "11ch" }} size="small" />
        <TextField label="Commission Overide" sx={{ m: 1, width: "11ch" }} size="small" />
        <TextField label="CRF" size="small" sx={{ m: 1, width: "11ch" }} />
        <TextField
          select
          label="Tail Number"
          defaultValue=" "
          SelectProps={{
            native: true,
          }}
          sx={{ m: 1, width: "11ch" }}
          size="small"
        >
          {aircraft.map((option) => (
            <option key={option.value} value={option.value}>
              {option.label}
            </option>
          ))}
        </TextField>
        <TextField
          label="Panorama Seat"
          select
          defaultValue=" "
          SelectProps={{
            native: true,
          }}
          sx={{ m: 1, width: "11ch" }}
          size="small"
        >
          {dummyData.map((option) => (
            <option key={option.value} value={option.value}>
              {option.label}
            </option>
          ))}
        </TextField>
        <TextField
          label="Charge Charter?"
          select
          defaultValue=" "
          SelectProps={{
            native: true,
          }}
          sx={{ m: 1, width: "11ch" }}
          size="small"
        >
          {chargeCharter.map((option) => (
            <option key={option.value} value={option.value}>
              {option.label}
            </option>
          ))}
        </TextField>
        <TextField label="Remarks" sx={{ m: 1, width: "36ch" }} size="small" rows={4} />
        {/* <Autocomplete
          disablePortal
          id="combo-box-demo"
          options={aircraft}
          sx={{ m: 1, width: "11ch" }}
          size="small"
          renderInput={(params) => <TextField {...params} label="Movie" />}
        /> */}
        <MDButton variant="gradient" color="dark" size="small" sx={{ m: 1 }}>
          Back
        </MDButton>
        <MDButton variant="gradient" color="error" size="small" sx={{ m: 1 }}>
          Book Now
        </MDButton>
      </div>
    </Box>
  );
}
