import { useState, useEffect } from "react";

// prop-types is a library for typechecking of props.
import PropTypes from "prop-types";

// @mui material components
import Card from "@mui/material/Card";
import Grid from "@mui/material/Grid";
// import AppBar from "@mui/material/AppBar";
// import Tabs from "@mui/material/Tabs";
// import Tab from "@mui/material/Tab";
import Icon from "@mui/material/Icon";

// Material Dashboard 2 React components
import MDBox from "components/MDBox";
import MDTypography from "components/MDTypography";
import MDAvatar from "components/MDAvatar";
import MDButton from "components/MDButton";

// Material Dashboard 2 React base styles
import breakpoints from "assets/theme/base/breakpoints";

// Images
import atlantis from "assets/images/atlantis.jpg";
import abudhabi from "assets/images/abudhabi.jpg";
// import backgroundImage from "assets/images/bg-profile.jpeg";

function Header({ children }) {
  const [tabsOrientation, setTabsOrientation] = useState("horizontal");
  // const [tabValue, setTabValue] = useState(0);

  useEffect(() => {
    // A function that sets the orientation state of the tabs.
    function handleTabsOrientation() {
      return window.innerWidth < breakpoints.values.sm
        ? setTabsOrientation("vertical")
        : setTabsOrientation("horizontal");
    }

    /** 
     The event listener that's calling the handleTabsOrientation function when resizing the window.
    */
    window.addEventListener("resize", handleTabsOrientation);

    // Call the handleTabsOrientation function to set the state with the initial value.
    handleTabsOrientation();

    // Remove event listener on cleanup
    return () => window.removeEventListener("resize", handleTabsOrientation);
  }, [tabsOrientation]);

  // const handleSetTabValue = (event, newValue) => setTabValue(newValue);

  return (
    <MDBox position="relative" mb={6}>
      <Grid container spacing={0}>
        <Grid item xs={12} md={6} xl={6}>
          <Card
            sx={{
              position: "relative",
              mt: 1,
              mx: 3,
              py: 6,
              px: 2,
            }}
          >
            <Grid container spacing={3} alignItems="center">
              <Grid item xs={12} md={6} lg={10} sx={{ ml: "auto" }}>
                <Grid container spacing={3} alignItems="center">
                  <Grid item md={3}>
                    <MDAvatar src={atlantis} alt="profile-image" size="xl" shadow="sm" />
                  </Grid>
                  <Grid item md={6}>
                    <MDBox height="100%" mt={0.5} lineHeight={1}>
                      <MDTypography variant="h5" fontWeight="medium">
                        Atlantis
                      </MDTypography>
                      <MDTypography variant="button" color="text" fontWeight="regular">
                        Luxury living Atlantis
                      </MDTypography>
                    </MDBox>
                  </Grid>
                </Grid>
              </Grid>
              <Grid item xs={12} md={6} lg={10} sx={{ ml: "auto" }}>
                <MDButton size="medium" variant="gradient" color="dark" href="#">
                  <Icon sx={{ fontWeight: "normal" }}>event_available</Icon>
                  &nbsp;Atlantis Avtive Bookings
                </MDButton>
                <MDButton size="medium" variant="gradient" color="light" href="#">
                  <Icon sx={{ fontWeight: "normal" }}>edit_calendar</Icon>
                  &nbsp;Atlantis Upcoming Bookings
                </MDButton>
                <MDButton size="medium" variant="gradient" color="dark" href="#">
                  <Icon sx={{ fontWeight: "normal" }}>event_busy</Icon>
                  &nbsp;Atlantis Cancelled Bookings
                </MDButton>
              </Grid>
            </Grid>
            {children}
          </Card>
        </Grid>
        <Grid item xs={12} md={6} xl={6}>
          <Card
            sx={{
              position: "relative",
              mt: 1,
              mx: 3,
              py: 6,
              px: 2,
            }}
          >
            <Grid container spacing={3} alignItems="center">
              <Grid item xs={12} md={6} lg={10} sx={{ ml: "auto" }}>
                <Grid container spacing={3} alignItems="center">
                  <Grid item md={3}>
                    <MDAvatar src={abudhabi} alt="profile-image" size="xl" shadow="sm" />
                  </Grid>
                  <Grid item md={6}>
                    <MDBox height="100%" mt={0.5} lineHeight={1}>
                      <MDTypography variant="h5" fontWeight="medium">
                        Abu Dhabi
                      </MDTypography>
                      <MDTypography variant="button" color="text" fontWeight="regular">
                        Luxury living Abu Dhabi
                      </MDTypography>
                    </MDBox>
                  </Grid>
                </Grid>
              </Grid>
              <Grid item xs={12} md={6} lg={10} sx={{ ml: "auto" }} alignItems="center">
                <MDButton size="medium" variant="gradient" color="dark" href="#">
                  <Icon sx={{ fontWeight: "normal" }}>event_available</Icon>
                  &nbsp;Abu Dhabi Avtive Bookings
                </MDButton>
                <MDButton size="medium" variant="gradient" color="light" href="#">
                  <Icon sx={{ fontWeight: "normal" }}>edit_calendar</Icon>
                  &nbsp;Abu Dhabi Upcoming Bookings
                </MDButton>
                <MDButton size="medium" variant="gradient" color="dark" href="#">
                  <Icon sx={{ fontWeight: "normal" }}>event_busy</Icon>
                  &nbsp;Abu Dhabi Cancelled Bookings
                </MDButton>
              </Grid>
            </Grid>
            {children}
          </Card>
        </Grid>
      </Grid>
    </MDBox>
  );
}

// Setting default props for the Header
Header.defaultProps = {
  children: "",
};

// Typechecking props for the Header
Header.propTypes = {
  children: PropTypes.node,
};

export default Header;
